import React, { FC } from 'react';

interface Arguments {
    title: JSX.Element;
    description: JSX.Element;
    worstCase: JSX.Element;
    avgCase: JSX.Element;
    bestCase: JSX.Element;
    space: JSX.Element;
}

const SortInfo: FC <Arguments> = ({
  title,
  description,
  worstCase,
  avgCase,
  bestCase,
  space
}) => {
  return (
    <div className="SortInfo">
      <hr />

      <div className="SortInfo__Body">
        <div className="SortInfo__Article">
          <h1>{title ? title : 'Select Algorithm'}</h1>
          <article>
            {description ? (
              description
            ) : (
              <p>
              You must select an algorithm before you can visualize it's
              execution on an array of numbers.
              </p>
            )}
          </article>
        </div>

        <aside className="SortInfo__Aside">
          <h3>Performance</h3>
          <table>
            <tbody>
              <tr>
                <td>Worst-case time complexity</td>
                <td>
                  <code>{worstCase}</code>
                </td>
              </tr>

              <tr>
                <td>Average time complexity</td>
                <td>
                  <code>{avgCase}</code>
                </td>
              </tr>

              <tr>
                <td>Best-case time complexity</td>
                <td>
                  <code>{bestCase}</code>
                </td>
              </tr>

              <tr>
                <td>Worst-case space complexity</td>
                <td>
                  <code>{space}</code>
                </td>
              </tr>
            </tbody>
          </table>
        </aside>
      </div>
    </div>
  );
};

export default SortInfo;
